.editorWrapper {
  max-width: 600px;
  margin: 1.125rem auto;
}

.expanded {
  width: 96%;
  min-height: 100vh;
  margin: auto;
}

.toolbar {
  display: flex;
  align-items: center;
  background-color: #4aa3a3;
  padding: 0.3rem;
  border: 1px solid #1d2f2f;
  box-shadow: 1px 1px 10px 2px #3a5f5f;
  font-family: Russo One;
  font-size: 1rem;
  color: black;
}

.editorWrapper .toolbar {
  width: 99%;
}

.toolbar i {
  width: 25px;
  margin-left: 5px;
}

.fa {
  display: inline-block;
  font: normal normal normal 14px / 1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.faFreeCodeCamp {
  margin-right: 3px;
}

.faFreeCodeCamp:before {
  content: "\f2c5";
}

.toolbar .faArrowsAlt {
  margin-left: auto;
}

.faArrowsAlt:hover {
  color: #57e2b1;
  cursor: pointer;
}

.faArrowsAlt:before {
  content: "\f0b2";
}

.toolbar .faCompress {
  margin-left: auto;
}

.faCompress:before {
  content: "\f066";
}

.faCompress:hover {
  color: #57e2b1;
  cursor: pointer;
}

.editorWrapper textarea {
  width: 99%;
  margin-bottom: -5px;
  resize: vertical;
  outline: none;
  padding-left: 5px;
  padding-top: 5px;
  font-size: 0.875rem;

  background-color: #c0d8d8;
  border: 1px solid #1d2f2f;
  box-shadow: 1px 1px 10px 2px #3a5f5f;
  border-top: none;
}
